import {Component, OnDestroy, OnInit} from '@angular/core';
import {Track} from '../track';
import {TracksComponent} from '../tracks/tracks.component';
import {FliktraxService} from '../fliktrax.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-by-genre',
  templateUrl: './by-genre.component.html',
  styleUrls: ['./by-genre.component.css']
})
export class ByGenreComponent implements OnInit {
  public genre: string;

  constructor(private flikTraxService: FliktraxService, private route: ActivatedRoute) {
    this.route.params.subscribe( params => console.log(params));
  }
  public tracks: Track[] = [];

  ngOnInit() {

    this.genre = this.route.snapshot.params.genre;

    this.flikTraxService.getTracksByCategory(this.genre)
      .subscribe(tracks => {
        this.tracks = tracks;
      });
  }
}
