import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {FliktraxService} from '../fliktrax.service';
import {Track} from '../track';

@Component({
  selector: 'app-by-mood',
  templateUrl: './by-mood.component.html',
  styleUrls: ['./by-mood.component.css']
})
export class ByMoodComponent implements OnInit {
  public mood: string;
  public tracks: Track[] = [];

  constructor(private flikTraxService: FliktraxService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.mood = this.route.snapshot.params.mood;

    this.flikTraxService.getTracksByCategory(this.mood)
      .subscribe(tracks => {
        this.tracks = tracks;
      });
  }

}
