import {Component, OnInit, Input, Output, EventEmitter, ViewChild} from '@angular/core';
import {TrackList} from '../TrackList';
import {Howl} from 'howler';
import { SlideInOutAnimation } from './animations';

@Component({
  selector: 'app-tracks',
  templateUrl: './tracks.component.html',
  styleUrls: ['./tracks.component.css'],
  animations: [SlideInOutAnimation]
})


export class TracksComponent implements OnInit {
  hideme = {};
  animationState: string[];
  constructor() {
    this.hideme = {};
    this.animationState = [];
  }

  @Input() songs: TrackList;
  public sound;
  ngOnInit() {
    for (let i = 0; i < 100; i++) {
      this.animationState.push('out');
    }
  }

  playTrack(songUrl: string) {
    if (this.sound) {
      this.sound.stop();
    }
    this.sound = new Howl({
      src: [songUrl],
      html5 : true
    });

    this.sound.play();
  }

  toggleShowDiv(divName: string) {
      console.log(this.animationState[divName]);
      this.animationState[divName] = this.animationState[divName] === 'out' ? 'in' : 'out';
      console.log(this.animationState);
  }


  stopTrack() {
   // this.myEvent.emit(this.sound);
  }

}
