import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {FliktraxService} from '../fliktrax.service';
import {Track} from '../track';
import {TracksComponent} from '../tracks/tracks.component';
import {Category} from '../category';
import {CategoriesComponent} from '../categories/categories.component';
import {Mood} from '../moods.model';
import {SlideInOutAnimation} from '../tracks/animations';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  animations: [SlideInOutAnimation]
})
export class HomeComponent implements OnInit {

  title = 'audiotram';
  constructor(private flikTraxService: FliktraxService) {}
  public tracks: Track[] = [];
  public categories: Category[] = [];
  public moods: Mood[] = [];
  public pause = false;
  public genreState = 'out';
  public moodsState = 'out';

  @ViewChild(TracksComponent) trackComponent;
  @ViewChild(CategoriesComponent) categoryComponent;
  public name = 'resting';

  ngOnInit() {
    this.flikTraxService.getCategories()
      .subscribe(categories => {
        this.categories = categories;
      });
    this.flikTraxService.getMoods()
      .subscribe(moods => {
        this.moods = moods;
      });
    this.flikTraxService.getTracks()
      .subscribe(tracks => {
        this.tracks = tracks;
      });

  }

  stopTrack() {
    this.trackComponent.sound.stop();
  }

  getDuration() {
    if (this.trackComponent.sound != null) {
      return this.trackComponent.sound.duration(this.trackComponent.sound.id);
    }
  }
  toggleShowDiv() {
    console.log(this.genreState);
    this.genreState = this.genreState === 'out' ? 'in' : 'out';
    this.moodsState='out';
    console.log(this.genreState);
  }
  moodToggleShowDiv(){
    this.genreState = 'out';
    this.moodsState = this.moodsState === 'out' ? 'in' : 'out';
  }
  ScrollTo(id) {
    const element = document.querySelector(id)
    const top = element.getBoundingClientRect().top
    window.scrollTo({
      top, // scroll so that the element is at the top of the view
      behavior: 'smooth' // smooth scroll
    })
  }
  pauseTrack() {
    if (!this.pause) {
      this.trackComponent.sound.pause();
      this.pause = true;
      return this.pause;
    } else {
      this.trackComponent.sound.play();
      this.pause = false;
      return this.pause;
    }
  }
}
