import { RouterModule, Routes } from '@angular/router';
import {ByGenreComponent} from './by-genre/by-genre.component';
import {HomeComponent} from './home/home.component';
import {ByMoodComponent} from './by-mood/by-mood.component';

export const routes: Routes = [
  { path: 'tracks-by-genre/:genre', component: ByGenreComponent},
  { path: 'tracks-by-mood/:mood', component: ByMoodComponent },
  { path: 'tracks-by-genre', component: ByGenreComponent},
  { path: 'tracksgenre', component: ByGenreComponent},
  { path: 'home', component: HomeComponent},
  { path: '', component: HomeComponent}
];

export const AppRoutingModule = RouterModule.forRoot(routes);
