import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import 'rxjs/add/operator/map';
import {Track} from './track';
import {CategoryList, MoodList, TrackList} from './TrackList';
import {Category} from './category';
import {Mood} from './moods.model';

@Injectable({
  providedIn: 'root'
})

export class FliktraxService {

  constructor(private httpClient: HttpClient) { }

  public getTracks(): Observable<Track[]> {
    let headers = new HttpHeaders();
    headers = headers.append('Authorization', 'Basic ' + btoa('audiotram:TSEBaY7q'));
    headers = headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const response = this.httpClient.get<TrackList>('http://api.fliktrax.com/search?searchterm=Hip%20Hop', {headers: headers})
      .map(list => list._embedded.search);
    return response;
  }

  public getCategories(): Observable<Category[]> {
    let headers = new HttpHeaders();
    headers = headers.append('Authorization', 'Basic ' + btoa('audiotram:TSEBaY7q'));
    headers = headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const response = this.httpClient.get<CategoryList>('https://api.fliktrax.com/categories', {headers: headers})
      .map(list => list._embedded.categories);
    return response;
  }

  public getMoods(): Observable<Mood[]> {
    let headers = new HttpHeaders();
    headers = headers.append('Authorization', 'Basic ' + btoa('audiotram:TSEBaY7q'));
    headers = headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const response = this.httpClient.get<MoodList>('https://api.fliktrax.com/moods', {headers: headers})
      .map(list => list._embedded.moods);
    return response;
  }

  public getTracksByCategory(genre: string): Observable<Track[]> {
    let headers = new HttpHeaders();
    headers = headers.append('Authorization', 'Basic ' + btoa('audiotram:TSEBaY7q'));
    headers = headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const response = this.httpClient.get<TrackList>('http://api.fliktrax.com/search?searchterm=' + genre, {headers: headers})
      .map(list => list._embedded.search);
    return response;
  }
}
